<template>
  <div
    class="NDT-Seat"
    :class="seatClass"
    v-b-tooltip
    :title="seatTitle"
    @click="reserve"
  />
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'NDTSeat',

    props: {
      readOnly: {
        type: Boolean,
        default: false
      },

      x: {
        type: Number,
        default: null
      },

      y: {
        type: Number,
        default: null
      },

      seatType: {
        type: String,
        default: null
      },

      mySeat: {
        type: Boolean,
        default: false
      },

      reserving: false
    },

    data: _ => ({
      isReserving: false
    }),

    computed: {
      ...mapGetters({
        seats: 'seatmap/seats',
        token: 'auth/token',
        user: 'user/user'
      }),

      seat () {
        return this.seats
          .find(({ x, y }) => x === this.x && y === this.y)
      },

      seatClass () {
        const { seat, seatType: type, user } = this
        return {
          'NDT-Seat--Free': type === 'free' || (seat && seat.status === 'free'),
          'NDT-Seat--Taken': type === 'taken' || (seat && seat.status === 'taken'),
          'NDT-Seat--Disabled': !this.mySeat && (type === 'disabled' || (seat && seat.status === 'reserved')),
          'NDT-Seat--Reserving': this.reserving,
          'NDT-Seat--MySeat': this.mySeat
        }
      },

      seatTitle () {
        if (this.seat) {
          if (!this.seat.user) {
            return `Rad: ${ this.seat.row }, Plass: ${ this.seat.seat }`
          }
          if (this.user && this.seat.user[0] === this.user.id) {
            return `Rad: ${ this.seat.row }, Plass: ${ this.seat.seat } (Min reservasjon)`
          }
          return `Rad: ${ this.seat.row }, Plass: ${ this.seat.seat } (${this.seat.user[1]})`
        }

        return false
      }
    },

    methods: {
      ...mapActions({
        setSeats: 'seatmap/setSeats',
        setWidth: 'seatmap/setWidth',
        setHeight: 'seatmap/setHeight',
        showAuth: 'modals/showAuth'
      }),

      async reserve () {
        if (!this.readOnly) {
          if (!this.user) {
            this.showAuth()
          } else {
            if (!this.mySeat) {
              if (this.seat.status === 'free' || (this.user && this.seat.user && this.seat.user[0] === this.user.id)) {
                this.isReserving = true
                this.$emit('reserve', true)
                try {
                  const reserved = await this.$api.put(`seatmap/reserve/${this.seat.id}`, {
                    owner: this.token
                  })
                  const seatmap = await this.$api.get('seatmap')
                  this.setSeats(seatmap.seats)
                  this.setWidth(seatmap.width)
                  this.setHeight(seatmap.height)
                  if (reserved) {
                    this.$emit('reserved', this.seat)
                  }
                } catch (error) {
                  console.error(error)
                } finally {
                  this.$emit('reserve', false)
                  this.isReserving = false
                }
              }
            }
          }
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/scss/variables";

  .NDT-Seat {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: .25rem;

    &--Disabled, &--Free, &--Taken {
      border: 1px solid $dark-gray;
    }

    &--Free {
      cursor: pointer;
      background: #00ff00;
      opacity: .8;
      &:hover {
        opacity: 1;
      }
    }

    &--Taken {
      background: #ff0000;
    }

    &--MySeat {
      background: #0178ff;
    }

    &--Disabled {
      background: gold;
    }

    &--Reserving {
      opacity: 0.5;
      pointer-events: none;
    }
  }
</style>
