<template>
  <div class="NDT-Payment">
    <h3 class="NDT-Payment__Event">
      NDT-LAN {{ currentEvent.name }}
    </h3>

    <h4 class="NDT-Payment__Ticket">
      {{ ticket.name }}
    </h4>
    <h4 class="NDT-Payment__Seat">
      {{ mySeat.title }}
    </h4>

    <h5 class="NDT-Payment__Price">
      Pris: {{ ticket.price }},-
    </h5>

    <b-button size="lg" @click="checkout" variant="success" :disabled="isPaying">
      <icon :icon="isPaying ? 'spinner' : ''" :spin="isPaying" /> Betal nå
    </b-button>
  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'NDTPayment',

    data: _ => ({
      isPaying: false
    }),

    computed: {
      ...mapGetters({
        user: 'user/user',
        ticketTypes: 'tickets/types',
        mySeat: 'seatmap/mySeat',
        authToken: 'auth/token',
        currentEvent: 'events/current',
        selectedTicket: 'tickets/selected'
      }),

      ticket () {
        return this.ticketTypes.find(ticket => ticket.id === this.selectedTicket)
      }
    },

    methods: {
      ...mapActions({
        setSignups: 'user/setSignups'
      }),

      checkout () {
        this.isPaying = true
        if (this.ticket.price) {
          this.$checkout.open({
            name: 'NDT-LAN',
            name: this.user.name,
            email: this.user.email,
            amount: this.ticket.price * 100,
            token: async (token) => {
              await this.$api.post('pay', {
                ticket: this.ticket.id,
                user: this.authToken,
                token: token.id
              })
              const signups = await this.$api.get(`signups?user=${this.authToken}`)
              this.setSignups(signups)
              this.isPaying = false
            }
          })
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/scss/variables";

  .NDT-Payment {
    width: 100%;
    text-align: center;

    &__Event {
      color: $lime;
      margin-bottom: 2rem;
    }

    &__Price {
      margin-top: 1rem;
      margin-bottom: 2rem;
    }
  }
</style>
