<template>
  <div />
</template>

<script>
  import { mapActions } from 'vuex'

  export default {
    name: 'Logut',

    methods: {
      ...mapActions({
        setToken: 'auth/setToken',
        setUser: 'user/setUser',
        hideAuth: 'modals/hideAuth'
      })
    },

    mounted () {
      this.hideAuth()
      this.setUser(null)
      this.setToken(null)
      this.$router.replace({ name: 'home' })
    }
  }
</script>
