<template>
  <div class="NDT-Tickets">
    <b-form>
      <b-container>
        <b-row class="NDT-Tickets__Container">
          <b-col lg="6" v-for="ticket in ticketTypes" :key="ticket.id">
              <b-card text-variant="white" class="NDT-Tickets__Ticket" @click="selectTicket(ticket.id)" :class="{ 'NDT-Tickets__Ticket--Selected': selectedTicket === ticket.id }">
                <h4 class="card-title" slot="header">
                  {{ ticket.name }}
                </h4>
                <p v-html="ticket.description" class="NDT-Tickets__Ticket__Description" />
                <div class="NDT-Tickets__Ticket__Price">
                  <strong>Pris: </strong> {{ ticket.price }},-
                </div>
              </b-card>
          </b-col>
        </b-row>
      </b-container>
    </b-form>
  </div>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'

  export default {
    name: 'NDTTicket',

    computed: {
      ...mapGetters({
        ticketTypes: 'tickets/types',
        selectedTicket: 'tickets/selected',
        mySeat: 'seatmap/mySeat'
      })
    },

    methods: {
      ...mapActions({
        setTicketTypes: 'tickets/setTicketTypes',
        selectTicket: 'tickets/selectTicket'
      })
    },

    async mounted () {
      const ticketTypes = await this.$api.get('ticket-types')
      this.setTicketTypes(ticketTypes)
      if (!this.selectedTicket || !this.ticketTypes.find(type => type.id === this.selectedTicket))  {
        this.selectTicket(this.ticketTypes[0].id)
      }
    }
  }
</script>

