  @import "../assets/scss/variables";

  .NDT-Tickets {
    &__Container {
      justify-content: center;
    }

    &__Ticket {
      cursor: pointer;
      transition: all ease-in-out .1s;
      margin-bottom: 1rem;
      background-color: $dark-gray;

      /deep/ .card-title {
        color: $lime;
      }

      &--Selected {
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
      }

      &__Price {
        color: $lime;
        font-style: italic;
      }
    }
  }
</style>
