<template>
  <div class="NDT-Seatmap">
    <b-container>
      <div v-if="!readOnly" class="NDT-Seatmap__Loader" :class="{ 'NDT-Seatmap__Loader--show': reserving || mySeat }">
        <template v-if="reserving">
          <h5>
            <icon
              spin
              icon="spinner"
              class="NDT-Seatmap__Loading__Spinner"
            />
            Oppdaterer reservasjon
          </h5>
        </template>
        <template v-else>
          <template v-if="mySeatAvailable">
            <h5 class="NDT-Seatmap__Reservation">
              <b-button variant="danger" size="sm" class="NDT-Seatmap__Reservation__Delete" @click="clearReservation">
                <icon icon="trash" class="NDT-Seatmap__Reservation__Delete__Icon" />
                &nbsp;Slett reservasjon
              </b-button>
              &nbsp;
              <vue-countdown :time="reservationTime" @countdownend="fetchSeatmap">
                <template slot-scope="props">
                  Reservasjon utløper {{ props.minutes }}:{{ props.seconds }}</template>
              </vue-countdown>
            </h5>
          </template>
          <template v-else>
            <h5>&nbsp;</h5>
          </template>
        </template>
      </div>
    </b-container>

    <template v-if="!seats.length || fetchingSeatmap">
      <h5>
        <icon
          spin
          icon="spinner"
          class="NDT-Seatmap__Loading__Spinner"
        />
        Henter kart...
      </h5>
    </template>
    <template v-else>
      <section class="NDT-Seatmap__Available">
        {{ available }} av {{ seats.length }} plasser ledig
      </section>

      <template v-if="explanation">
        <section class="NDT-Seatmap__Explanation">
          <div class="NDT-Seatmap__Preview">
            <ndt-seat seat-type="free" />
            <span>Ledig</span>
          </div>
          <div class="NDT-Seatmap__Preview">
            <ndt-seat seat-type="taken" my-seat />
            <span>Min plass</span>
          </div>
          <div class="NDT-Seatmap__Preview">
            <ndt-seat seat-type="disabled" />
            <span>Reservert</span>
          </div>
          <div class="NDT-Seatmap__Preview">
            <ndt-seat seat-type="taken" />
            <span>Bestilt</span>
          </div>
        </section>
      </template>

      <div class="NDT-Seatmap__Seatmap">
        <div
          v-for="row in height"
          :key="`row-${row - 1}`"
          class="NDT-Seatmap__Row"
        >
          <div
            v-for="col in width"
            :key="`row-${col - 1}`"
            class="NDT-Seatmap__Column"
          >
            <ndt-seat
              :x="col - 1"
              :y="row - 1"
              :reserving="reserving"
              :my-seat="mySeat && mySeat.x === col - 1 && mySeat.y === row - 1"
              :readOnly="readOnly"
              @reserve="reserve"
              @reserved="reserved"
            />
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'
  import NdtSeat from '@/components/NdtSeat'
  import VueCountdown from '@xkeshi/vue-countdown'
  import moment from 'moment'

  export default {
    name: 'NDTSeatmap',

    components: {
      NdtSeat,
      VueCountdown
    },

    props: {
      readOnly: {
        type: Boolean,
        default: false
      },

      explanation: {
        type: Boolean,
        default: true
      }
    },

    data: _ => ({
      selected: null,
      fetchingSeatmap: false,
      reserving: false,
      updateTimer: null
    }),

    computed: {
      ...mapGetters({
        width: 'seatmap/width',
        height: 'seatmap/height',
        available: 'seatmap/available',
        seats: 'seatmap/seats',
        user: 'user/user',
        mySeat: 'seatmap/mySeat',
        token: 'auth/token'
      }),

      reservationTime () {
        if (this.mySeat) {
          const now = moment()
          const expires = moment(this.mySeat.reserved)
          const duration = moment.duration(expires.diff(now))
          return duration.asMilliseconds()
        }

        return 0
      },

      mySeatAvailable () {
        if (this.mySeat) {
          return this.reservationTime > 0
        }

        return false
      }
    },

    methods: {
      ...mapActions({
        setSeats: 'seatmap/setSeats',
        setWidth: 'seatmap/setWidth',
        setHeight: 'seatmap/setHeight',
      }),

      async clearReservation () {
        if (this.mySeat) {
          this.reserve(true)
          await this.$api.delete(`seatmap/reserve/${this.mySeat.id}`, {
            owner: this.token
          })
          await this.fetchSeatmap()
          this.reserve(false)
        }
      },

      reserve (reserving) {
        this.reserving = reserving
      },

      reserved (seat) {
        this.$emit('complete-step', seat)
      },

      async fetchSeatmap () {
        if (!this.seats || !this.seats.length) {
          this.fetchingSeatmap = true
        }
        const seatmap = await this.$api.get('seatmap')
        this.setSeats(seatmap.seats)
        this.setWidth(seatmap.width)
        this.setHeight(seatmap.height)
        if (this.fetchingSeatmap) {
          this.fetchingSeatmap = false
        }
      }
    },

    mounted () {
      this.fetchSeatmap()
      this.updateTimer = setInterval(() => this.fetchSeatmap(), 1000 * 60)
    }
  }
</script>

