      &__Spinner {
        margin-right: 1rem;
      }
    }

    &__Loader {
      opacity: 0;
      transition: opacity ease-in-out .1s;

      &--show {
        opacity: 1;
      }
    }

    &__Reservation {
      display: flex;
      align-items: center;
      max-height: 1.5rem;

      &__Delete {
        margin-right: 1rem;

        &__Icon {
          margin-right: .25rem;
        }
      }
    }

    &__Explanation {
      display: flex;
      padding: 1rem;
      background: $dark-gray;
      border: 1px solid $dark-gray;
      border-radius: .25rem;
      grid-area: 'preview';
    }

    &__Available {
      padding: 1rem;
      border: 1px solid transparent;
      grid-area: 'available';
      font-size: 1.25rem;
    }

    &__Preview {
      display: flex;
      padding-right: 1rem;
      align-items: center;

      span {
        margin-left: .5rem;

        &::before {
          content: ' ';
        }
      }
    }

    &__Seatmap {
      grid-area: 'map';
      max-width: 100%;
      overflow: auto;
    }

    &__Row {
      width: 100%;
      display: flex;
      flex-direction: row;
      margin-top: .25rem;
    }

    &__Column {
      margin-left: .25rem;
    }
  }
</style>
