<template>
  <div class="NDT-Tickets">
    <template v-if="!user || !hasSignup">
      <template v-if="ticketSaleAvailable && !ticketSaleClosed">
        <section class="NDT-Tickets__Steps">
          <template>
            <div :class="seatmapClass" @click="changeStep('seatmap')">
              <icon :icon="seatmapIcon" class="NDT-Tickets__Steps__Step__Icon" />
              <span class="NDT-Tickets__Steps__Step__Label">Velg plass</span>
            </div>
            <div :class="ticketClass" @click="changeStep('ticket')">
              <icon :icon="ticketIcon" class="NDT-Tickets__Steps__Step__Icon" />
              <span class="NDT-Tickets__Steps__Step__Label">Velg billett</span>
            </div>
            <div :class="paymentClass" @click="changeStep('payment')">
              <icon :icon="paymentIcon" class="NDT-Tickets__Steps__Step__Icon" />
              <span class="NDT-Tickets__Steps__Step__Label">Betaling</span>
            </div>
          </template>
        </section>

        <div class="NDT-Tickets__Nav">
          <b-button size="sm" class="NDT-Tickets__Previous" :disabled="!previousEnabled" @click="decrementStep">
            <icon icon="arrow-left" class="NDT-Tickets__Previous__NavIcon" />
            <span class="NDT-Tickets__Previous__NavLabel">Forrige</span>
          </b-button>
          <b-button size="sm" class="NDT-Tickets__Next" :disabled="!nextEnabled" @click="incrementStep">
            <span class="NDT-Tickets__Next__NavLabel">Neste</span>
            <icon icon="arrow-right" class="NDT-Tickets__Next__NavIcon" />
          </b-button>
        </div>

        <template v-if="step">
          <component :key="step" :is="steps[step].component" class="NDT-Tickets__Step" />
        </template>
      </template>
      <template v-else>
        <template v-if="ticketSaleClosed">
          <section class="NDT-Tickets__Steps">
            <p class="NDT-Tickets__Unavailable">
              Billettsalget er avsluttet.
            </p>
          </section>
          <component :is="steps.seatmap.component" read-only class="NDT-Tickets__Step" />
        </template>
        <template v-else>
          <section class="NDT-Tickets__Steps">
            <p class="NDT-Tickets__Unavailable">
              <template v-if="event.signupStart">
                <vue-countdown :time="signupStart" :leading-zero="false" @countdownend="refresh">
                  <template slot-scope="props">
                    <p>
                      Salget starter om <strong>
                        {{ props.days }} dager, {{ props.hours }} timer, {{ props.minutes }} minutter, og {{ props.seconds }} sekunder
                      </strong>
                    </p>
                  </template>
                </vue-countdown>
              </template>
              <template v-else>
                Vi kommer tilbake med mer informasjon snart.
              </template>
            </p>
          </section>
        </template>
      </template>
    </template>
    <template v-else>
        <p class="NDT-Tickets__MyTicket">
          Du har reservert plass til {{ event.name }}<br>
          Vi har sendt billett til deg på e-post ({{ user.email }})
        </p>
      <component :is="steps.seatmap.component" read-only class="NDT-Tickets__Step" />
    </template>
  </div>
</template>

<script>
  import NdtSeatmap from '@/components/NdtSeatmap'
  import NdtTicket from '@/components/NdtTicket'
  import NdtPayment from '@/components/NdtPayment'
  import { mapGetters } from 'vuex'
  import moment from 'moment'
  import VueCountdown from '@xkeshi/vue-countdown'

  export default {
    name: 'Tickets',

    data: _ => ({
      step: null
    }),

    components: {
      VueCountdown
    },

    methods: {
      refresh () {
        window.location.reload()
      },

      incrementStep () {
        const previous = this.step
        const steps = Object.keys(this.steps)

        const i = steps.indexOf(previous)

        if (i !== -1) {
          if (steps[i + 1]) {
            this.step = steps[i + 1]
          }
        } else {
          this.step = steps[0]
        }

        if (previous) {
          this.completeStep(previous)
        }
      },

      decrementStep () {
        const steps = Object.keys(this.steps)
        const i = steps.indexOf(this.step)
        if (i !== -1 && steps[i - 1]) {
          this.step = steps[i - 1]
        }
      },

      completeStep (step = null) {
        this.steps[step || this.step].completed = true
      },

      changeStep (step) {
        if (this.steps[step].completed) {
          this.step = step
        }
      }
    },

    mounted () {
      const steps = Object.keys(this.steps)

      steps.forEach(step => {
        if (this.steps[step].completed) {
          const next = steps.indexOf(step)
          this.step = step
          if (next !== -1 && steps[next + 1]) {
            this.step = steps[next + 1]
          }
        }
      })

      if (!this.step) {
        this.incrementStep()
      }
    },

    watch: {
      mySeat (seat) {
        if (!seat) {
          this.step = Object.keys(this.steps)[0]
        }
      }
    },

    computed: {
      ...mapGetters({
        mySeat: 'seatmap/mySeat',
        event: 'events/current',
        selectedTicket: 'tickets/selected',
        hasSignup: 'user/hasSignup',
        user: 'user/user'
      }),

      ticketSaleAvailable () {
        if (this.event) {
          const { signupStart } = this.event
          if (signupStart) {
            const now = moment()
            const start = moment(signupStart)
            return now.isAfter(start)
          }
        }
      },

      ticketSaleClosed () {
        if (this.event) {
          const { signupStart, signupStop } = this.event
          if (signupStop) {
            const now = moment()
            const stop = moment(signupStop)
            return now.isAfter(stop)
          }
        }
      },

      steps () {
        return {
          seatmap: {
            completed: Boolean(this.mySeat),
            component: NdtSeatmap
          },
          ticket: {
            completed: Boolean(this.selectedTicket),
            component: NdtTicket
          },
          payment: {
            completed: false,
            component: NdtPayment
          }
        }
      },

      seatmapClass () {
        const active = this.step === 'seatmap'
        return {
          'NDT-Tickets__Steps__Step': true,
          'NDT-Tickets__Steps__Step--Active': active
        }
      },

      signupStart () {
        return moment(this.event.signupStart).diff(moment(), 'millisecond')
      },

      seatmapIcon () {
        return this.steps.seatmap.completed
          ? 'check-circle'
          : 'circle'
      },

      nextEnabled () {
        const steps = Object.keys(this.steps)
        const i = steps.indexOf(this.step)
        const complete = this.steps[this.step].completed
        const next = i !== -1 && this.steps[steps[i + 1]]
        return complete && next
      },

      previousEnabled () {
        const steps = Object.keys(this.steps)
        const i = steps.indexOf(this.step)
        const next = i !== -1 && this.steps[steps[i - 1]]
        return Boolean(next)
      },

      ticketClass () {
        const active = this.step === 'ticket'
        return {
          'NDT-Tickets__Steps__Step': true,
          'NDT-Tickets__Steps__Step--Active': active
        }
      },

      ticketIcon () {
        return this.steps.ticket.completed
          ? 'check-circle'
          : 'circle'
      },

      paymentClass () {
        const active = this.step === 'payment'
        return {
          'NDT-Tickets__Steps__Step': true,
          'NDT-Tickets__Steps__Step--Active': active
        }
      },

      paymentIcon () {
        return this.steps.payment.completed
          ? 'check-circle'
          : 'circle'
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/scss/variables";

  .NDT-Tickets {
    &__Steps {
      width: 100%;
      display: flex;
      justify-content: center;

      &__Step {
        display: flex;
        flex-direction: column;
        margin-left: 1rem;
        margin-right: 1rem;

        &__Icon {
          margin: 0 auto;
          font-size: 1.5rem;
        }

        &__Label {
          margin-top: .5rem;
        }

        &--Active {
          color: $lime;
          cursor: pointer;
        }
      }
    }

    &__Unavailable {
      color: $lime;
      font-size: 1.5rem;
      padding: 2rem;
    }

    &__MyTicket {
      width: 100%;
      font-size: 1.25rem;
    }

    &__MyTicket__Download {
      width: 100%;
      font-size: 1.5rem;
      color: $lime;
    }

    &__Step {
      margin-top: 1rem;
      margin-bottom: 1rem;
    }

    &__Nav {
      width: 100%;
      display: flex;
      margin-top: 1rem;
      justify-content: flex-end;
    }

    &__Previous {
      margin-right: 1rem;

      &__NavIcon {
        margin-right: 1rem;
      }
    }

    &__Next {
      &__NavIcon {
        margin-left: 1rem;
      }
    }
  }
</style>
