<template>
  <div class="Home">
    <template v-if="!fetchingArticles">
      <ndt-article
        v-for="article in sortedArticles"
        :key="article.id"
        :article-id="article.id"
        excerpt
      />
    </template>
    <template v-else>
      <div class="Home__Loading">
        <h5>
          <icon
            spin
            icon="spinner"
            class="Home__Loading__Spinner"
          />
          Henter artikler...
        </h5>
      </div>
    </template>
  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'
  import NdtArticle from '@/components/NdtArticle'

  export default {
    name: 'Home',

    components: {
      NdtArticle
    },

    data: _ => ({
      fetchingArticles: false
    }),

    computed: {
      ...mapGetters({
        articles: 'articles/articles'
      }),

      sortedArticles () {
        return this.articles
          .filter(_ => true)
          .sort((a, b) => {
            return b.id - a.id
          })
      }
    },

    methods: {
      ...mapActions({
        setArticles: 'articles/setArticles'
      })
    },

    async mounted () {
      if (!this.articles || !this.articles.length) {
        this.fetchingArticles = true
      }
      const articles = await this.$api.get('articles')

      this.setArticles(articles)

      if (this.fetchingArticles) {
        this.fetchingArticles = false
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/scss/variables";

  .Home {
    display: grid;
    grid-gap: 1rem;

    &__Loading {
      width: 100%;
      color: $lime;
      text-align: center;

      &__Spinner {
        margin-right: 1rem;
      }
    }
  }
</style>
